*{
  padding: 0;
  margin: 0;
}

.card > img{
  width: 200px;
  height: 260px;
  object-fit: cover;
  border-radius: 1rem;
}

.card > img:hover{
  box-shadow: 0px 0px 9px 4px rgba(255, 255, 255, 0.763);
}

.board{
  
  padding: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
  gap: 15px;
  margin: 1rem 5rem;

}


@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

@media only screen and (min-width:320px) {

  body{
    background-color: black;
    padding: 0 .5rem
  }

  .header{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: space-between;
    align-items: center;
    color: white;
    font-family: 'Press Start 2P', cursive;
    font-size: 10px;
  }

  .header > div > img{
    border-radius: 50%;
    width: 150px;
  }
  
  .header > div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .header > .score{
    font-size: 8px;
  }
  
}


@media only screen and (min-width:1024px){
  .header{
    font-size: 20px;
  }

  .header > div > img{
    width: 225px;
  }

  .header > .score{
    font-size: 15px;
  }
} 



